import React from "react";
import styled from "styled-components";
import "animate.css/animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';

const Nucleus = styled.div`
  width: 50px;
  height: 50px;
  background: darkgrey;
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  margin: auto;
`;

const Orbit = styled.div`
  height: ${(props) => props.orbitalnumber * 40 + 80}px;
  width: ${(props) => props.orbitalnumber * 40 + 80}px;
  border: 1px solid #ccc;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 50%;
`;

const SmallElectron = styled.div`
width: 10px;
height: 10px;
background: cyan;
border-radius: 50%;
position: absolute;
top: 0;
bottom: 0;
left: 0;
right: 0;
overflow: hidden;
margin: auto;
animation: ${(props) =>
    "circle" + props.en.toString() + props.electronorbit.toString() + props.degree.toString()}
  ${(props) => props.electronorbit * 2}s linear infinite;
@keyframes ${(props) =>
    "circle" + props.en.toString() + props.electronorbit.toString() + props.degree.toString()} {
  0% {
    transform: rotate(${(props) => props.en * props.degree - props.degree}deg)
      translate(${(props) => (props.electronorbit * 20 + 40) * -1}px)
      rotate(0deg);
  }
  100% {
    transform: rotate(
        ${(props) => props.en * props.degree + (360 - props.degree)}deg
      )
      translate(${(props) => (props.electronorbit * 20 + 40) * -1}px)
      rotate(-360deg);
  }
}
`;

