import React from 'react'
import periodicsong from "../assets/periodictablesong.mp3";

const Player = () => {
  return (
    <div className="pertable_audio">
        <audio className="audiocontroler" controls={true} src={periodicsong} />
        <p>Periodic Table Song ( ASAP SCIENCE )</p>
      </div>
  )
}

export default Player
