import React from 'react'

const Navbar = ({showMenu}) => {
  return (
    <div className={showMenu ? "sort_nav shown" : "sort_nav hidden"}>
        <p className="orange_color">Alkali Metals</p>
        <p className="yellow_color">Alkaline Earth Metals</p>
        <p className="pink_color">Lanthoids</p>
        <p className="purple_color">Aktinoids</p>
        <p className="red_color">Transition Metals</p>
        <p className="lightblue_color">Post-Transition Metals</p>
        <p className="lightgreen_color">Metalloids</p>
        <p className="green_color">Other NonMetals</p>
        <p className="blue_color">Noble Gases</p>
        <p className="silver_color">Unknown</p>
      </div>
  )
}

export default Navbar
