import React from "react";
import styled from "styled-components";

const Element = styled.div` 
  
`

const Nucleus = styled.div`
  width: 50px;
  height: 50px;
  background: darkgrey;
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  margin: auto;
`;

const Orbit = styled.div`
  height: ${(props) => props.orbitalnumber * 40 + 80}px;
  width: ${(props) => props.orbitalnumber * 40 + 80}px;
  border: 1px solid #ccc;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 50%;
`;

const SmallElectron = styled.div`
width: 10px;
height: 10px;
background: cyan;
border-radius: 50%;
position: absolute;
top: 0;
bottom: 0;
left: 0;
right: 0;
overflow: hidden;
margin: auto;
animation: ${(props) =>
    "circle" + props.en.toString() + props.electronorbit.toString() + props.degree.toString()}
  ${(props) => props.electronorbit * 2}s linear infinite;
@keyframes ${(props) =>
    "circle" + props.en.toString() + props.electronorbit.toString() + props.degree.toString()} {
  0% {
    transform: rotate(${(props) => props.en * props.degree - props.degree}deg)
      translate(${(props) => (props.electronorbit * 20 + 40) * -1}px)
      rotate(0deg);
  }
  100% {
    transform: rotate(
        ${(props) => props.en * props.degree + (360 - props.degree)}deg
      )
      translate(${(props) => (props.electronorbit * 20 + 40) * -1}px)
      rotate(-360deg);
  }
}
`;

const Row1 = ({setShowAtom, setAtomConfig}) => {
  return (
    <div className="row row1">
          <div className="row_elements_part">
            <Element
              onClick={() => {
                setShowAtom(true);
                setAtomConfig({
                  symbol: "H",
                  name: "Hydrogen",
                  mass: 1.008,
                  atomicNumber: 1,
                  electrons_configuration: {
                    orbit1: 1,
                    orbit2: 0,
                    orbit3: 0,
                    orbit4: 0,
                    orbit5: 0,
                    orbit6: 0,
                    orbit7: 0,
                  },
                });
              }}
              className="element green"
            >

              {window.screen.width < 1025 && <div className="fortouch atom_modal">
                <Nucleus className="nucleus" />
                <Orbit orbitalnumber={1}>
                  <SmallElectron degree={0} en={1} electronorbit={1} />
                </Orbit>
                <div className="electronic_configuration">
                  <h5>1</h5>
                </div>
              </div>}
              <p className="atomic_number">1</p>
              <h3 className="symbol">H</h3>
              <h4 className="full_element_name">Hydrogen</h4>
              <p className="atomic_mass">1.008</p>
            </Element>
          </div>
          <div className="row_elements_part">
            <Element
              onClick={() => {
                setShowAtom(true);
                setAtomConfig({
                  symbol: "He",
                  name: "Helium",
                  mass: 4.0026,
                  atomicNumber: 2,
                  electrons_configuration: {
                    orbit1: 2,
                    orbit2: 0,
                    orbit3: 0,
                    orbit4: 0,
                    orbit5: 0,
                    orbit6: 0,
                    orbit7: 0,
                  },
                });
              }}
              className="element blue"
            >

              {window.screen.width < 1025 && <div className="fortouch atom_modal">
                <Nucleus className="nucleus" />
                <Orbit orbitalnumber={1}>
                  <SmallElectron degree={180} en={1} electronorbit={1} />
                  <SmallElectron degree={180} en={2} electronorbit={1} />
                </Orbit>
                <div className="electronic_configuration">
                  <h5>2</h5>
                </div>
              </div>}
              <p className="atomic_number">2</p>
              <h3 className="symbol">He</h3>
              <h4 className="full_element_name">Helium</h4>
              <p className="atomic_mass">4.0026</p>
            </Element>
          </div>
        </div>
  )
}

export default Row1
